.experince {
     position: relative;
     font-size: 22px;
     line-height: 25px;
     width: 100%;
     height: 600px;

     h3 {
          position: relative;
          font-size: 50px;
          line-height: 1.5em;
          text-align: center;
          margin-bottom: 40px;

          &::after {
               content: "";
               display: inline-block;
               width: 150px;
               height: 5px;
               position: absolute;
               right: 50%;
               bottom: 0px;
               background-color: #FBFF22;
               box-shadow: 3px 0 15px 5px #FBFF22;

               @media (max-width: 500px) {
                    width: 100%;
                    right: 0;
               };
          }
     }
     
     &__wrapper {
          position: relative;
          text-align: center;
          width: 100%;
          height: 80%;
          font-size: 15px;

          div {
               position: absolute;


               &:nth-child(odd) {
                    text-align-last: center;
                    top: 10px;

                    @media (min-width:1265px) {
                         font-size: 17px;
                         left: 150px;
                    };

                    @media (max-width: 500px) {
                         left: 0;
                         font-size: 12px;
                         z-index: 4;

                         &:not(:last-child) {

                              &::after {
                                   content: "";
                                   z-index: 1;
                                   display: inline-block;
                                   position: absolute;
                                   left: 160px;
                                   top: 100px;
                                   width: 60%;
                                   height: 1px;
                                   transform: rotate(15deg);
                                   background-color: #FBFF22;
                                   box-shadow: 3px 0 5px 1px #fbff22;
     
                                   @media (max-width: 465px) {
                                        width: 50%;
                                        top: 90px;
          
                                   };
     
                                   @media (max-width: 386px) {
                                        width: 40%;
                                   };
                              }
                         }

                         &:first-child {
                              &::after {
                                   content: "";
                                   z-index: 1;
                                   display: inline-block;
                                   position: absolute;
                                   left: 160px;
                                   top: 50px;
                              }
                         }

                    };

               }

               &:nth-child(even) {
                    top: 10px;
                    right: 0;
                    
                    @media (min-width: 1265px) {
                         font-size: 17px;
                         right: 150px;
                    };

                    @media (max-width: 500px) {
                         right: 0;
                         font-size: 12px;

                         &::after {
                              content: "";
                              z-index: 1;
                              display: inline-block;
                              position: absolute;
                              right: 120px;
                              top: 80px;
                              width: 100%;
                              height: 1px;
                              transform: rotate(-15deg);
                              background-color: red;
                              box-shadow: 3px 0 5px 1px #fbff22;

                              @media (max-width: 465px) {
                                   width: 60%;
                                   right: 120px;
                              };

                              @media (max-width: 386px) {
                                   width: 40%;
                              };

                              &:last-child {
                                   width: 1000px !important;
                              }
                         }

                  };

                  p {
                    white-space: nowrap;
                  }
               }
          }

          @media (min-width: 1450px) {
               padding: 0 60px;
          };

          @media (min-width: 501px) {
               background: url("../../assets/mainPage/exper.jpg") no-repeat center/contain;
          };
     }

     &__atom {
          
     }

     &__first-client {
        top: 60px !important;
     }

     &__info-chigans {
          top: 120px !important;          
     }

     &__agency-first {
          top: 220px !important;
     }

     &__agency-second {
          top: 270px !important;

          #war {
               color: red ;
          }
     }

     &__personal-consult {
          top: 340px !important;
     }

     &__total-amount {
          top: 410px !important;
     }

     &__cover {
          position: absolute;
          left: 46%;
          top: 20%;
          width: 90px;
          height: 100%;
          background-color: black;
          transition: all 4.3s ease 0s;

          @media (max-width: 1120px) {
               display: none;
          };
     }

     @media (min-width: 750px) {
          padding: 0 30px;
     };

     @media (max-width: 500px) {
          margin-top: 50px;
     };

     span {
          font-size: 15px;
          color: #FBFF22;
     }
     
}
